import React from 'react'

// Import typefaces
import 'typeface-montserrat'
import 'typeface-merriweather'

import profilePic from './profile-pic.jpg'

class Bio extends React.Component {
  render() {
    return (
      <div>
        <img
          src={profilePic}
          alt={`Kyle Mathews`}
        />
        <p>
          Written by <strong>Jordyn McGinnis</strong> who lives in Utah building useful things.{' '}
          <a href="https://twitter.com/jordynm4">
            You should follow her on Twitter
          </a>
        </p>
      </div>
    )
  }
}

export default Bio



